
input {
  width: 20%;
  margin: 6px 5px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid black;
  text-align: center;
}
p {
  margin: 4px 0
}
input:focus {
  border: none;
  border-radius: 5px;
}
button {
  padding: 10px 14px;
  margin: 10px 0;
}
.container {
  width: 320px;
  margin: 0 auto;
}
.box {
  border: 1px solid black;
  border-radius: 4px;
  padding: 5px 10px;
  margin: 5px 0
}
.sqf {
  font-size: 70%;
  vertical-align: text-top;
}
.decimal-input {
  width: 10%;
}




